/** Css for the settings card in the first page
*/
.settings-card {
	width: 80vw;
	box-shadow: 0 0 10px rgb(180, 180, 180);
}

.cursor-pointer:hover {
	cursor: pointer;
}

@media screen and (min-width: 1000px) {
	.settings-card {
		width: 50vw;
	}
	
}

.settings-nav:hover {
	cursor: pointer;
}

/** Css for the status 
*/
.status-card {
	width: 75%;
	box-shadow: 0 0 10px rgb(180, 180, 180);
}

/** Cards sizing is crucial to show the whole board one one screen. 
		Cards are as wide as possible (20% of the view width) but cannot
		have too big of height. The hight limit is for wide screens.
*/
.memory-card {
	margin: 0.5vw;
	width: 20vw;
	height: 20vw;
	max-width: 18vh;
	max-height: 18vh;
	box-shadow: 0 0 10px rgb(180, 180, 180);
}

.memory-card:hover {
	cursor: pointer;
}

.memory-card-back {
	background-color: skyblue;
}

.front-image {
	width: 100%;
}

.back-image {
	width: 75%;
}